import { Component, OnInit ,ViewEncapsulation } from '@angular/core';
import { Router} from "@angular/router";
import { TranslateService } from '@ngx-translate/core';

import { AuthService } from '../../service/auth/auth.service';
import { ZXingScannerComponent } from '@zxing/ngx-scanner';


@Component({
   selector: 'ms-loginone-session',
   templateUrl:'./loginone-component.html',
   styleUrls: ['./loginone-component.scss'],
   encapsulation: ViewEncapsulation.None,
})

export class LoginoneComponent {

   email    : string;
   password : string;
   name     :string;

   scanner: ZXingScannerComponent;

   hasCameras = false;
   hasPermission: boolean;
   qrResultString: string;
   qrResultsArray: Array<string> = [];

   constructor( private router: Router,
                private authService : AuthService,
                public translate: TranslateService) { }


                ngOnInit() {

                }

                handleQrCodeResult(resultString: string) {
                  this.qrResultString = resultString;
                  if (!this.qrResultsArray.includes(resultString)) {
                    this.qrResultsArray.push(resultString);
                  }
                }
              
                handleQrCodeValidation(resultString: string) {
                  console.log('Validation: ', resultString);
                  
                }
              
                onDeviceSelectChange(selectedValue: string) {
                  console.log('Selection changed: ', selectedValue);
                }

                
   // when email and password is correct, user logged in.
   logIn(value){
      this.authService.loginUser(value);
   }
	
}



